.cell {
  height: 39px;
  width: 39px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  background: #ffffff;
  border: 1px solid #dddddd;
  transition: all 0.3s ease 0s;
}
.text {
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
}
.clicked {
  margin: 2.5px;
  height: 34px;
  width: 34px;
  border-color:#ffd205;
  background: #ffd205;
}
